@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737373}.container{margin:0 auto;max-width:1280px}

.hero{padding-bottom:178px;background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality/static/media/hero-bg.5d86912d.jpg);background-size:cover}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation-item{margin-left:74px;font-weight:normal;font-size:19px;line-height:22px;text-decoration:none;color:#ffffff}.hero-navigation-item:first-child{margin-left:0}.hero-title{margin:108px 0 132px;width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#ffffff}.hero-link{padding:35px 151px;display:inline-block;font-weight:normal;font-size:32px;line-height:37px;text-decoration:none;color:#ffffff;background:#B78A3F;border:none;cursor:pointer}

.facts{padding:100px 0 50px 0;position:relative;box-sizing:border-box}.facts-title{margin-bottom:40px;font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff}.facts-slide-desc{margin-top:40px;margin-right:0;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.facts>.swiper-button-prev,.facts .swiper-button-next{width:31px;height:58px;top:420px;color:#B78A3F}.facts>.swiper-button-prev{left:-60px}.facts>.swiper-button-next{right:-50px}.facts-pagination{display:flex;justify-content:center;margin-top:40px}.facts-pagination>.swiper-pagination-bullet{margin:0 20px;width:24px;height:24px;border-radius:50%;border:2px solid #B78A3F;display:inline-block;background-color:transparent;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#ffffff}.air-q-map{margin:42px 0;height:560px}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;text-align:justify;color:#ffffff}

.sectors{padding:50px 0 153px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#ffffff}.sectors-subtitle{margin:42px 0;font-weight:normal;font-size:23px;line-height:27px;text-align:justify;color:#ffffff}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{margin-bottom:42px;width:calc((100% / 3) - 70px);height:240px;position:relative;display:flex;align-items:center}.sectors-list-item:hover{background:rgba(0,0,0,0.83)}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{position:absolute;left:80%;width:400px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#DADADA;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;text-align:justify;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:90%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

